<template>
  <tas-base-crud ref="sub-module" :config="config" hide-update hide-filter hide-detail>
    <template v-slot:list-table-action="data">
      <a @click="openModal('modal-rincian-beasiswa', data)" class="btn btn-info btn-sm">Lihat Detail</a>
    </template>
    <template v-slot:list-header>
      <b-modal header-class="pb-1" no-close-on-backdrop v-if="modalData.detail" id="modal-rincian-beasiswa" hide-footer size="xl">
        <template #modal-header>
          <div class="d-flex justify-content-between flex-fill">
            <div class="d-flex flex-column flex-fill">
              <div class="d-flex justify-content-between">
                <div class="text-center mb-5 flex-fill">
                  <h5 class="mb-1 font-weight-bold">Detail {{ config.title }}</h5>
                  <span class="font-weight-bolder">Periode {{ modalData.detail.periode_month | parse('period') }}</span>
                </div>
                <div>
                  <i class="ri-close-circle-line text-danger ri-2x pointer" @click="$bvModal.hide('modal-rincian-beasiswa')"></i>
                  <!-- <button class="btn btn-icon"><i class="ri-close-circle-line text-danger ri-5x"></i></button> -->
                </div>
              </div>
              <table class="table no-border mb-0">
                <tr>
                  <td class="pt-0 pl-0 nowrap-table">Tenaga Kerja</td>
                  <td class="pt-0 pr-0 nowrap-table">: {{ modalData.rel_employee_id }}</td>
                  <td class=""></td>
                  <td class="pt-0 pl-0 nowrap-table">Jabatan</td>
                  <td class="pt-0 pr-0 nowrap-table">: {{ modalData.detail.rel_job_position_id }}</td>
                </tr>
                <tr>
                  <td class="pt-0 pl-0 nowrap-table">MPP</td>
                  <td class="pt-0 pr-0 nowrap-table">: {{ modalData.detail.is_mpp | parse('ynBool') }}</td>
                  <td class=""></td>
                  <td class="pt-0 pl-0 nowrap-table">Status Ketenagakerjaan</td>
                  <td class="pt-0 pr-0 nowrap-table">: {{ modalData.detail.rel_status_employment_id }}</td>
                </tr>
              </table>
            </div>
          </div>
        </template>
        <div class="tables-responsive">
          <table class="table table-header-custom v-center">
            <thead>
              <tr>
                <th>Nama Lengkap &amp; Hubungan Keluarga</th>
                <th class="max-w-150px">Pendidikan</th>
                <th class="max-w-150px">Peringkat</th>
                <th class="max-w-150px">Uang Beasiswa</th>
              </tr>
            </thead>
            <tbody>
              <template v-if="modalData.detail.data_tunjangan_beasiswa_components.length">
                <tr v-for="(e,i) in modalData.detail.data_tunjangan_beasiswa_components" :key="i+'-data-employee'">
                  <td class="max-w-250px">
                    <label class="checkbox">
                      <input v-model="e.active" type="checkbox" :true-value="1" :false-value="0">
                      <span class="mr-3"></span>
                      <div class=" d-flex justify-content-between align-items-center flex-fill">
                        <div class="text-line-2 max-w-200px"> {{ e.rel_employee_family_id }} <span class="d-block font-size-sm"><b>Status</b> : {{ e.rel_family_relationship_id }}</span>
                        </div>
                      </div>
                    </label>
                  </td>
                  <template v-if="e.active">
                    <td class="max-w-150px">
                      <s-input v-if="datasets.education_id.length" v-model="e.education_id" :item="{ attr:[{clearable: false}], type:'select', validation:[], placeholder: 'Pilih Jenjang Pendidikan', api:'', nm:true, notAppendToBody:true, optionList:datasets.education_id , ic:'w-100 mt-2' }" />
                    </td>
                    <td class="max-w-150px">
                      <s-input v-if="datasets.rank.length" v-model="e.rank" :item="{ attr:[{clearable: false}], type:'select', validation:[], placeholder: 'Peringkat', api:'', nm:true, notAppendToBody:true, optionList:datasets.rank , ic:'w-100 mt-2' }" />
                    </td>
                    <td class="max-w-150px">
                      <s-input v-model="e.component_value" :item="{ type:'number', validation:[], label:null, prefix: 'Rp. ', groupClass:'mb-0', ic:'w-100 mr-1', api:'', attr: { precision:2 } }" />
                    </td>
                  </template>
                  <td v-else colspan="3" class="bg-light-primary">
                    <p class="mb-0 pl-2">Checklist apabila Anggota Keluarga ini mendapatkan beasiswa</p>
                  </td>
                </tr>
              </template>
              <tr v-else>
                <td colspan="4" class="text-center">
                  <p class="mb-0">Tidak ada Anggota Keluarga yang terdaftar</p>
                </td>
              </tr>
              <tr>
                <th colspan="3" class="text-right text-uppercase">Jumlah Penerimaan</th>
                <th>
                  <div class="d-flex justify-content-between">
                    <span class="">Rp.</span>
                    <span class="text-right font-weight-bolder">{{ modalData.penerimaan_value || 0 | parse('number') }}</span>
                  </div>
                </th>
              </tr>
            </tbody>
          </table>
        </div>
        <div class="mt-5">
          <button @click="submitData('modal-rincian-bills')" class="btn btn-primary btn-lg btn-block font-size-md">Simpan &amp; Tutup Detail</button>
        </div>
      </b-modal>
    </template>
  </tas-base-crud>
</template>

<script>
export default {
  name: 'crud-tunjangan-beasiswa-details',
  data () {
    return {
      modalData: {},
      mainKey: 0,
      config: {
        title: 'Data Beasiswa Anak',
        model_api: null,
        getter: 'custom/tunjangan-beasiswa-details',
        setter: 'tunjangan-beasiswa-details',
        pk_field: 'name',
        filter_api: {},
        custom_api: {
          create: 'custom/tunjangan-beasiswa-details'
        },
        permission: {
          create: 'create-tunjangan-beasiswa-details',
          read: 'view-tunjangan-beasiswa-details',
          update: 'update-tunjangan-beasiswa-details',
          delete: 'delete-tunjangan-beasiswa-details'
        },
        slave: [],
        fields: [
          { id: 'id', methods: { list: false, detail: false, create: false, update: false, filter: false } },
          { id: 'updated_by', methods: { list: false, detail: false, create: false, update: false, filter: false } },
          { id: 'created_by', methods: { list: false, detail: false, create: false, update: false, filter: false } },
          { id: 'created_at', label: 'Dibuat', methods: { create: false, update: false, filter: false } },
          { id: 'updated_at', label: 'Diperbarui', methods: { list: false, detail: false, create: false, update: false, filter: false } },
          {
            id: 'status_code',
            data: 'status_code',
            label: 'Status',
            placeholder: null,
            methods: {
              list: {
                order: true,
                class: { 'not_calculated_yet': 'badge badge-danger', 'calculated': 'badge badge-primary' },
                transform: 'calculated'
              },
              detail: {
                order: true,
                class: { 'not_calculated_yet': 'badge badge-danger', 'calculated': 'badge badge-primary' },
                transform: 'calculated'
              },
              create: false,
              update: {
                view_data: 'status_code',
                type: 'radio',
                value: 'calculated',
                option: {
                  list_pointer: {
                    label: 'label',
                    code: 'code',
                    list: [{ label: 'Sudah dihitung', code: 'calculated' }, { label: 'Belum dihitung', code: 'not_calculated_yet' }]
                  }
                }
              },
              filter: {
                view_data: 'status_code',
                type: 'radio',
                option: {
                  list_pointer: {
                    label: 'label',
                    code: 'code',
                    list: [{ label: 'Sudah dihitung', code: 'calculated' }, { label: 'Belum dihitung', code: 'not_calculated_yet' }]
                  }
                }
              }
            }
          },
          {
            id: 'employee_id',
            label: 'Nama Tenaga Kerja',
            methods: {
              list: { view_data: 'rel_employee_id' },
              create: {
                setter: 'employees',
                getter: 'employees',
                type: 'lookup-radio',
                option: {
                  list_pointer: {
                    label: 'fullname', code: 'id', display: ['nip', 'fullname']
                  }
                }
              },
              update: {
                setter: 'employees',
                getter: 'employees',
                type: 'lookup-radio',
                option: {
                  list_pointer: {
                    label: 'fullname', code: 'id', display: ['nip', 'fullname']
                  }
                }
              },
              filter: {
                setter: 'employees',
                getter: 'employees',
                type: 'lookup-radio',
                option: {
                  list_pointer: {
                    label: 'fullname', code: 'id', display: ['nip', 'fullname']
                  }
                }
              }
            }
          },
          {
            id: 'job_position_id',
            label: 'Jabatan',
            methods: {
              list: { view_data: 'rel_job_position_id' },
              create: false,
              update: {
                setter: 'job-positions',
                getter: 'job-positions',
                type: 'lookup-radio',
                option: {
                  list_pointer: {
                    label: 'job_position_name', code: 'id', display: ['job_position_name']
                  }
                }
              },
              filter: {
                setter: 'job-positions',
                getter: 'job-positions',
                type: 'lookup-radio',
                option: {
                  list_pointer: {
                    label: 'job_position_name', code: 'id', display: ['job_position_name']
                  }
                }
              }
            }
          },
          {
            id: 'status_employment_id',
            label: 'Status Ketenagakerjaan',
            methods: {
              list: { view_data: 'rel_status_employment_id', show: true },
              create: false,
              update: {
                setter: 'custom/master-status-employments',
                getter: 'custom/master-status-employments',
                type: 'lookup-radio',
                option: {
                  list_pointer: {
                    label: 'name', code: 'id', display: ['name']
                  },
                  get_param: [{ 'id': 'job_position_id' }]
                }
              },
              filter: {
                setter: 'custom/master-status-employments',
                getter: 'custom/master-status-employments',
                type: 'lookup-radio',
                option: {
                  list_pointer: {
                    label: 'name', code: 'id', display: ['name']
                  },
                  get_param: [{ 'id': 'job_position_id' }]
                }
              }
            }
          },
          { id: 'penerimaan_value', label: 'Penerimaan', methods: { create: false, update: false, list: { transform: 'money' }, detail: { transform: 'money' } } },
          { id: 'description', label: 'Deskripsi', methods: { create: { type: 'textarea' }, update: { type: 'textarea' } } }
        ]
      },
      datasets: {
        rank: [
          { label: 'Peringkat I', code: 1 },
          { label: 'Peringkat II', code: 2 },
          { label: 'Peringkat III', code: 3 },
          { label: 'Peringkat IV', code: 4 },
          { label: 'Peringkat V', code: 5 }
        ],
        education_id: []
      }
    }
  },
  watch: {
    mainKey: {
      immediate: false,
      deep: false,
      handler (e) {
        this.$refs['sub-module'].$children[1].getData()
      }
    }
  },
  methods: {
    async openModal (id, data) {
      const pendidikan = await this.getDatasets('master-educations', null, null)
      this.$set(this.datasets, 'education_id', pendidikan.map(x => { return { label: x.name, code: x.id } }))
      const detail = {
        detail: await this.getDetail('get', 'custom/tunjangan-beasiswa-details/show', { id: data.rowData.id })
      }
      this.modalData = Object.assign({}, detail, data.rowData)
      this.$nextTick().then(e => {
        this.$bvModal.show(id)
      })
    },
    getDetail (method, api, param, obj = null) {
      return this.$_api[method](api, param).then(res => {
        return obj ? res.data[obj] || res.data : res.data
      }).catch((err) => {
        this.$_alert.error(err)
      })
    },
    copyValue (obj = null) {
      if (!obj) {
        this.$set(this.modalData, 'detail', { ...this.modalData.detail, ...this.modalData.sync.data_recaps })
      }
    },
    getDatasets (api, param, obj = null) {
      return this.$_api.dataset(api, param).then(res => {
        return obj ? res.data[obj] || res.data : res.data
      }).catch((err) => {
        this.$_alert.error(err)
      })
    },
    submitData () {
      const api = this.config.getter + '/calculate'
      this.$_api.post(api, this.modalData.detail).then(res => {
        this.$_alert.success(null, 'Detail Rincian berhasil diperbarui')
        // this.$set(this.modalData, 'detail', res.data)
        this.$bvModal.hide('modal-rincian-beasiswa')
        this.mainKey++
      }).catch((err) => {
        this.$_alert.error(err)
      })
    }
  }
}
</script>



